@font-face {
  font-family: "Geometria";
  src: url("../fonts/Geometria.ttf") format("ttf"), url("../fonts/Geometria.eot") format("eot"), url("../fonts/Geometria.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Geometria-Bold";
  src: url("../fonts/Geometria-Bold.ttf") format("ttf"), url("../fonts/Geometria-Bold.eot") format("eot"), url("../fonts/Geometria-Bold.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  color: #000;
  text-transform: none;
  text-decoration: none;
}
a:hover {
  color: #000;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
h1,
h2 {
  margin: 0;
  padding: 0;
}
div {
  box-sizing: border-box;
}
img {
  width: 100%;
  height: auto;
}
form {
  margin: 0;
}
.button {
  display: inline-block;
  padding: 16px 30px;
  font-size: 13px;
  line-height: 16px;
  font-family: 'Gilroy', Arial, sans-serif;
  color: #fff;
  background-color: #0072c7;
  border: 1px solid #0072c7;
  border-radius: 2px;
  white-space: nowrap;
  cursor: pointer;
  box-sizing: border-box;
}
.button:hover {
  color: #fff;
}
.button:focus {
  outline: none;
  background-color: #0072c7;
}
.layout__wrapper {
  max-width: 1260px;
  margin: 180px auto 100px auto;
}
.layout__wrapper .result__serch {
  margin-bottom: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .layout__wrapper .result__serch {
    padding: 20px 10px 0 10px;
    border-top: 1px solid #dadada;
    flex-direction: column;
    align-items: flex-end;
  }
  .layout__wrapper .result__serch .choices {
    margin-right: 0;
    margin-bottom: 20px !important;
    width: 100%;
  }
}
.layout__wrapper .result__serch__title {
  font-size: 24px;
  line-height: 34px;
  font-family: 'Geometria', Arial, sans-serif;
  font-weight: 500;
  flex-grow: 1;
}
@media (max-width: 767px) {
  .layout__wrapper .result__serch__title {
    display: none;
  }
}
.layout__wrapper .result__serch .checkbox__wrapper {
  margin-right: 20px;
  align-self: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
@media (max-width: 767px) {
  .layout__wrapper .result__serch .checkbox__wrapper {
    align-self: flex-end;
    margin-left: 0;
    margin-right: 6px;
  }
  .layout__wrapper .result__serch .checkbox__wrapper label {
    flex-direction: row-reverse;
  }
  .layout__wrapper .result__serch .checkbox__wrapper label:before {
    margin-right: 0;
    margin-left: 10px;
  }
  .layout__wrapper .result__serch .checkbox__wrapper label:after {
    left: auto;
    right: 4px;
  }
  .layout__wrapper .result__serch .checkbox__wrapper .tooltip__icon {
    order: 1;
  }
}
.layout__wrapper .result__serch .checkbox__wrapper input:checked+label {
  color: #0072c7;
}
.layout__wrapper .result__serch .checkbox__wrapper input:checked+label:before {
  border-color: #0072c7;
}
.layout__wrapper .result__serch .checkbox__wrapper input:checked+label:after {
  background: none;
  border: 1px solid #0072c7;
  border-top-color: transparent;
  border-right-color: transparent;
  height: 6px;
  transform: rotate(-45deg);
}
.layout__wrapper .result__serch .view-mode__wrapper {
  align-self: center;
}
.button-no-bg {
  background-color: transparent;
  border-color: transparent;
  color: #a3a3a3;
  font-family: "Geometria", Arial, sans-serif;
}
.button-no-bg:hover {
  border-color: #003349;
  color: #003349;
}
.button-no-bg:focus {
  border-color: #003349;
  color: #003349;
  background-color: transparent;
}
.button-black {
  background-color: #003349;
  border-color: #003349;
  font-family: "Geometria", Arial, sans-serif;
}
.button-black:focus {
  background-color: #003349;
  border-color: #003349;
}
.checkbox__wrapper label {
  position: relative;
  cursor: pointer;
  color: #353535;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  font-family: "Geometria", Arial, sans-serif;
  font-size: 14px;
  line-height: 17px;
}
.checkbox__wrapper label:before {
  content: '';
  display: block;
  width: 20px;
  min-width: 20px;
  height: 20px;
  position: relative;
  margin-right: 10px;
  background-color: #fff;
  border: 1px solid #dadada;
  border-radius: 3px;
}
.checkbox__wrapper label:after {
  content: '';
  display: none;
  width: 12px;
  height: 14px;
  position: absolute;
  left: 5px;
  top: 4px;
  background: url("../img/planirovki-i-ceny/icons/send.svg") no-repeat 50% 50%;
  background-size: 100% auto;
}
.checkbox__wrapper input[type='checkbox'] {
  display: none;
}
.checkbox__wrapper input:checked+label:after {
  display: block;
}
.checkbox__wrapper input:checked+label {
  color: #003349;
}
.checkbox__wrapper input:checked+label:before {
  border-color: #003349;
}
.checkbox__wrapper input:invalid+label:before {
  border-color: #ff4747;
}
.checkbox__wrapper-btn {
  position: relative;
}
.checkbox__wrapper-btn label {
  color: #353535;
  font-family: "Geometria", Arial, sans-serif;
  font-size: 14px;
  line-height: 17px;
  border: 1px solid #dcdcdc;
  border-radius: 3px;
  cursor: pointer;
  min-width: 60px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  box-sizing: border-box;
  padding: 16px 26px;
}
.checkbox__wrapper-btn input[type='checkbox'] {
  width: 0.01px;
  height: 0.01px;
  position: absolute;
  left: -100px;
  top: 0;
  opacity: 0;
  z-index: -100;
}
.checkbox__wrapper-btn input[type='checkbox']:checked+label,
.checkbox__wrapper-btn input[type='checkbox']:checked:hover+label {
  color: #353535;
  background: rgba(220,220,220,0.2);
  border-color: #3a393c;
}
.checkbox__wrapper-btn input[type='checkbox']:hover+label {
  color: #353535;
  background: rgba(220,220,220,0.2);
  border-color: #3a393c;
}
.view-mode__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  position: relative;
}
@media (max-width: 1023px) {
  .view-mode__wrapper {
    display: none;
  }
}
.view-mode__wrapper input[type="radio"] {
  width: 0.01px;
  height: 0.01px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -100;
  opacity: 0;
}
.view-mode__wrapper label {
  width: 40px;
  min-width: 40px;
  height: 40px;
  margin-right: 10px;
  cursor: pointer;
}
.view-mode__wrapper label:last-child {
  margin-right: 0;
}
.view-mode__wrapper label svg {
  width: 100%;
  height: auto;
}
.view-mode__wrapper label:hover svg path {
  fill: #003349;
}
.view-mode__wrapper input[id="view-mode-tile"]:checked~label[for="view-mode-tile"] svg path,
.view-mode__wrapper input[id="view-mode-simple"]:checked~label[for="view-mode-simple"] svg path {
  fill: #003349;
}
.view-mode__wrapper input[id="view-mode-tile"]:checked~label[for="view-mode-tile"] svg rect,
.view-mode__wrapper input[id="view-mode-simple"]:checked~label[for="view-mode-simple"] svg rect {
  fill: rgba(220,220,220,0.2);
}
.filters__wrapper {
  position: relative;
}
.filters__wrapper__title {
  font-size: 24px;
  line-height: 34px;
  font-family: "Geometria", Arial, sans-serif;
  font-weight: 500;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .filters__wrapper__title {
    padding: 0 10px;
  }
}
.filters__wrapper>.button {
  width: 100%;
  text-align: center;
  display: none;
}
.filters__wrapper>.button .icon__wrapper {
  width: 14px;
  height: 14px;
  display: inline-block;
  margin-right: 14px;
}
.filters__wrapper>.button .icon__wrapper img {
  width: 100%;
  height: auto;
}
.filters__wrapper>.button .button__text {
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  font-family: "Geometria", Arial, sans-serif;
  display: inline-block;
  margin-right: 14px;
}
.filters__wrapper>.button .button__counter {
  display: inline-block;
  width: 16px;
  height: 16px;
  text-align: center;
  color: #0072c7;
  font-family: "Geometria", Arial, sans-serif;
  font-size: 12px;
  line-height: 14px;
  background-color: #fff;
  border-radius: 50%;
}
@media (max-width: 767px) {
  .filters__wrapper>.button {
    display: block;
    width: calc(100% - 20px);
    margin: 0 auto 20px auto;
  }
}
.filters__wrapper .filters__form {
  display: block;
}
.filters__wrapper .filters__form .close__filters__form {
  display: none;
}
.filters__wrapper .filters__form__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  height: 107px;
  overflow: hidden;
}
.filters__wrapper .filters__form__row-all {
  height: auto;
}
.filters__wrapper .filters__form__content {
  box-sizing: border-box;
}
.filters__wrapper .filters__form__cell {
  margin: 0 26px 30px 0;
}
.filters__wrapper .filters__form__label {
  color: #a3a3a3;
  margin-bottom: 10px;
  font-family: 'Geometria', Arial, sans-serif;
  font-size: 14px;
  line-height: 17px;
}
.filters__wrapper .filters__form .filters__wrapper__title {
  display: none;
}
.filters__wrapper .form__cell-small {
  width: 141px;
}
.filters__wrapper .form__cell-normal {
  width: 322px;
}
.filters__wrapper .checkbox__section .checkbox__wrapper {
  width: 140px;
  margin-bottom: 4px;
}
.filters__wrapper .checkbox__section .checkbox__wrapper:last-child {
  margin-bottom: 0;
}
.filters__wrapper .checkbox__section-number-rooms {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.filters__wrapper .checkbox__section-number-rooms .checkbox__wrapper-btn {
  margin-right: 20px;
}
.filters__wrapper .checkbox__section-number-rooms .checkbox__wrapper-btn:last-child {
  margin-right: 0;
}
@media (max-width: 767px) {
  .filters__wrapper .checkbox__section-number-rooms .checkbox__wrapper-btn {
    margin-right: 5px;
  }
}
.filters__wrapper .filters__actions__panel {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: row;
  padding-bottom: 40px;
  margin-bottom: 40px;
  border-bottom: 1px solid #dcdcdc;
  box-sizing: border-box;
}
.filters__wrapper .filters__actions__panel .all-parametrs__wrapper {
  flex-grow: 1;
}
.filters__wrapper .filters__actions__panel .all-parametrs__wrapper input[type="checkbox"] {
  width: 0.01px;
  height: 0.01px;
  opacity: 0;
  position: absolute;
  z-index: -100;
  left: 0;
  top: 0;
}
.filters__wrapper .filters__actions__panel .all-parametrs__label {
  color: #0072c7;
  font-family: "Geometria", Arial, sans-serif;
  line-height: 22px;
  font-size: 16px;
  font-weight: normal;
  cursor: pointer;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
}
.filters__wrapper .filters__actions__panel .all-parametrs__text:after {
  content: "";
  min-width: 7px;
  width: 7px;
  height: 7px;
  display: inline-block;
  margin-left: 7px;
  border: 1px solid #0072c7;
  border-top-width: 0;
  border-left-width: 0;
  transform: rotate(45deg) translateY(-4px);
}
.filters__wrapper .filters__actions__panel .all-parametrs__text.all-parametrs__show:after {
  margin-left: 10px;
  transform: rotate(225deg) translateY(-2px);
}
.filters__wrapper .filters__actions__panel .all-parametrs__show {
  display: none;
}
.filters__wrapper .filters__actions__panel .all-parametrs__wrapper input[type="checkbox"]:checked~.all-parametrs__hide {
  display: none;
}
.filters__wrapper .filters__actions__panel .all-parametrs__wrapper input[type="checkbox"]:checked~.all-parametrs__show {
  display: block;
}
.filters__wrapper .filters__actions__panel .button.button-no-bg {
  margin-right: 10px;
  text-transform: none;
  letter-spacing: 0;
}
@media (max-width: 767px) {
  .filters__wrapper .filters__form {
    display: none;
  }
  .filters__wrapper .filters__form-active {
    display: block;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 10000;
    overflow-y: auto;
  }
  .filters__wrapper .filters__form-active .close__filters__form {
    display: block;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
  }
  .filters__wrapper .filters__form-active .close__filters__form img {
    width: 100%;
    height: auto;
  }
  .filters__wrapper .filters__form__row {
    padding: 10px;
  }
  .filters__wrapper .filters__form__row-all {
    height: auto;
  }
  .filters__wrapper .filters__form__cell {
    margin-bottom: 20px;
    margin-right: 0;
    width: 100%;
  }
  .filters__wrapper .filters__form__cell.form__cell-small {
    width: 100%;
  }
  .filters__wrapper .filters__form__cell.form__cell-normal {
    width: 100%;
  }
  .filters__wrapper .filters__form .filters__wrapper__title {
    display: block;
    padding-left: 10px;
    padding-top: 30px;
    font-size: 24px;
    line-height: 34px;
    color: #3a393c;
    font-family: "Geometria", Arial, sans-serif;
    font-weight: 500;
  }
  .filters__wrapper .filters__form .filters__actions__panel {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row;
    flex-direction: column-reverse;
    padding: 0 10px;
    border-bottom-color: transparent;
  }
  .filters__wrapper .filters__form .filters__actions__panel .button {
    width: 100%;
  }
  .filters__wrapper .filters__form .filters__actions__panel .button-no-bg {
    border: 1px solid #003349;
    color: #003349;
  }
  .filters__wrapper .filters__form .filters__actions__panel .button-black {
    margin-bottom: 20px;
  }
  .filters__wrapper .filters__form__row-all {
    height: auto;
  }
  .filters__wrapper .filters__actions__panel .all-parametrs__wrapper {
    display: none;
  }
}
.cards__header__row {
  display: none;
  margin-bottom: 20px;
  padding: 0 20px;
}
.cards__header__row.cards__header-show {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
@media (max-width: 1023px) {
  .cards__header__row.cards__header-show {
    display: none;
  }
}
.cards__header__cell {
  color: #a3a3a3;
  font-size: 14px;
  line-height: 20px;
  font-family: 'Geometria', Arial, sans-serif;
  margin-right: 60px;
}
.cards__header-deadline {
  margin-right: 120px;
}
.cards__header-floor {
  margin-right: 135px;
}
.cards__header-group-row.cards__header-show {
  justify-content: space-between;
}
.cards__header-group-row .cards__header__cell {
  width: 100%;
  margin-right: 0;
}
.cards__header-group-row .cards__header__cell:first-child {
  max-width: 170px;
}
.cards__header-group-row .cards__header__cell:last-child {
  min-width: 260px;
}
.cards__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}
@media (max-width: 1300px) {
  .cards__wrapper {
    justify-content: space-around;
  }
}
.cards__wrapper .card__wrapper {
  width: 300px;
  border: 1px solid #dcdcdc;
