  padding: 51px 20px 20px 20px;
  margin: 0 20px 20px 0;
  font-family: "Geometria", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #3a393c;
  position: relative;
}
.cards__wrapper .card__wrapper:nth-child(4n+4) {
