.cards__wrapper .card__slider img {
  width: 100%;
  max-width: 200px;
  height: auto;
  max-height: 200px;
  margin: auto;
}
.cards__wrapper .card__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.cards__wrapper .card__row-meters-apartment {
  margin-bottom: 20px;
  white-space: nowrap;
}
.cards__wrapper .card__row-meters-apartment .card__cell-deadline {
